import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from 'src/environments/environment';


const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type': 'application/json',
    // 'Authorization': 'my-auth-token'
  })
};

@Injectable({
  providedIn: 'root'
})

export class BotService {
  
  baseUrl = environment.apiEndpoint + '/api/';

  constructor(private http: HttpClient) {
  }


  public getAllBots = () => {
    return this.http.get(this.baseUrl + 'Ai/Bots');
  }
  public createBots = (data: any) => this.http.post<any>(this.baseUrl + 'Ai/Bots', data, httpOptions);
  public removeBot = (botId: any) => {
    return this.http.delete<any>(this.baseUrl + 'Ai/Bots/' + botId);
  }
}
