import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { BotsComponent } from '../../bots/bots.component';

@Component({
  selector: 'app-bot-remove',
  templateUrl: './bot-remove.component.html',
  styleUrls: ['./bot-remove.component.css']
})
export class BotRemoveComponent implements OnInit {

  //variable
name: string;
currentBot: string;

  constructor(
    public dialogRef: MatDialogRef<BotsComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any
    ) { }

  ngOnInit() {
    this.currentBot= this.data.name;
  }

  removeBot(){
    this.dialogRef.close({action: 1, data: null});
  }
  onNoClick(): void {
    this.dialogRef.close();
  }

}
