import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CustomerComponent } from './customer.component';
import { BotsComponent } from './bots/bots.component';
import { OverviewComponent } from './overview/overview.component';
import { ProfilesComponent } from './profiles/profiles.component';
import { SupportsComponent } from './supports/supports.component';
import { GuidesComponent } from './guides/guides.component';


const routes: Routes = [
  {
    path: '', component: CustomerComponent,
    children: [

      {
        path: 'overviews', component: OverviewComponent
      },
      {
        path: 'bots', component: BotsComponent
      },
      {
        path: 'profiles', component: ProfilesComponent
      },
      {
        path: 'supports', component: SupportsComponent
      },
      {
        path: 'guides', component: GuidesComponent
      },

      // {
      //   path: '', redirectTo: 'overviews', pathMatch: 'full'
      // },
      {
        path: '', redirectTo: 'bots', pathMatch: 'full'
      },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class CustomerRoutingModule { }
