import { Component, OnInit } from '@angular/core';
import { BotCreateComponent } from '../dialog/bot-create/bot-create.component';
import { MatDialog, MatSnackBar } from '@angular/material';
import { BotService } from 'src/app/shared/services/bots.service';
import { BotRemoveComponent } from '../dialog/bot-remove/bot-remove.component';
import { Bot } from 'src/app/shared/models/bots.model';

@Component({
  selector: 'app-bots',
  templateUrl: './bots.component.html',
  styleUrls: ['./bots.component.css']
})
export class BotsComponent implements OnInit {

  //variable
  // bots: { name: string; isActive: boolean; botId: string; createAt: string; }[];
  // bot = [
  //   { name: 'Bot_1', isActive: true, botId: '132141fdsdgsdg', createAt: '20/12/2020:12:12:00' },
  //   { name: 'Bot_2', isActive: false, botId: '132141fdsdgsdg', createAt: '20/12/2020:12:12:00' },
  //   { name: 'Bot_3', isActive: false, botId: '132141fdsdgsdg', createAt: '20/12/2020:12:12:00' },
  //   { name: 'Bot_4', isActive: true, botId: '132141fdsdgsdg', createAt: '20/12/2020:12:12:00' },
  //   { name: 'Bot_5', isActive: false, botId: '132141fdsdgsdg', createAt: '20/12/2020:12:12:00' },
  //   { name: 'Bot_6', isActive: true, botId: '132141fdsdgsdg', createAt: '20/12/2020:12:12:00' },
  // ];
  loading = true;
  //variable
  bots: any = [];



  constructor(
    private botServer: BotService,
    public dialog: MatDialog,
    private _snackBar: MatSnackBar
    ) { }

  ngOnInit() {
    this.getAllBot();
  }

  // reload data bot
  refresh() {
    this.getAllBot();
  }

  // opne diaglog create bot
  diagCreateBot() {
    this.createBot();
  }
  // opne diaglog remove bot
  diagRemoveBot(data: any) {
    this.removeBot(data);
  }










  //get all bot
  getAllBot() {
    this.botServer.getAllBots()
      .subscribe(res => {

        //this.connectFail = false;
        this.bots = res['data'] as Bot[];
        localStorage.setItem('tpsoftbot_client', JSON.stringify(this.bots));
        setTimeout(() => {
          this.loading = false;
        }, 500);

      },
        error => {
          this.loading = false;
          this._snackBar.open('Đã có lỗi xảy ra', 'OK', {
            duration: 1000
          });
          //this.connectFail = true;
        }
      );
  }

  //save localstorage data info bot
  saveLocalStorageBot(data: any){
    localStorage.setItem('tpsoftbot_client', JSON.stringify(data));
  }

  //create bot
  createBot() {
    const dialogRef = this.dialog.open(BotCreateComponent, {
      data: null,
      width: '400px',
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result && result.action === 1) {
        this.botServer.createBots(result.data).subscribe(
          res => {
            if (res.status === 200) {
              this.getAllBot();
              this._snackBar.open('Thao tác thành công', 'OK', {
                duration: 1000
              });
            } else {
              this._snackBar.open('Thao tác không thành công', 'OK', {
                duration: 1000
              });
            }
          },
          error => {
            this._snackBar.open('Thao tác không thành công', 'OK', {
              duration: 1000
            });
            //this.connectFail = true;
          }
        );
      }
    });

  }

  //remove bot
  removeBot(bot:any) {
    const dialogRef = this.dialog.open(BotRemoveComponent, {
      data: bot,
      width: '400px',
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result && result.action === 1) {
        this.botServer.removeBot(bot.botId).subscribe(
          res => {
            if (res.status === 200) {
              this.getAllBot();
              this._snackBar.open('Thao tác thành công', 'OK', {
                duration: 1000
              });
            } else {
              this._snackBar.open('Thao tác không thành công', 'OK', {
                duration: 1000
              });
            }
          },
          error => {
            this._snackBar.open('Thao tác không thành công', 'OK', {
              duration: 1000
            });
            //this.connectFail = true;
          }
        );
      }
    });
  }

}
